import {useState} from 'react';
import Table from './components/Table';
import Slider from './components/Slider';
import {BrowserRouter as Router, Switch, Route, Link} from 'react-router-dom';
import dataParametrs from './data/dataTable.json';
import {Context} from './components/Context';
import { AppBar, Box, Container, Grid, Toolbar, Typography , Link as MuiLink, Divider } from '@material-ui/core';
import { createStyles, makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles((theme) => createStyles({
  headerLink : {
    textDecoration: 'none',
    fontSize: theme.spacing(2),
    color: 'white'
  }
}))
const reData = dataParametrs.reduce( (accum , parametr) => {
  accum[parametr.number] = parametr;
  return accum;
} , {});


function App() {

  const [dataTable , setDataTable] = useState(reData);
  const classes = useStyles();
  return (
    <>
      <Context.Provider value={[dataTable, setDataTable]}>
      <Router>
        <AppBar position='sticky'>
          <Toolbar>
            <Typography variant='h6' style={{flexGrow : 1}}>Миллиметры отклонений</Typography>
            <Divider orientation='vertical' flexItem style={{margin : 5}}/>
            <MuiLink to='/slider' component={Link} color='textSecondary' underline='none' variant='h6'>Перейти в последовательность</MuiLink>
            <Divider orientation='vertical' flexItem style={{margin : 5}}/>
            <MuiLink to='/' component={Link} color='textSecondary' underline='none' variant='h6'>Таблица</MuiLink>
          </Toolbar>
        </AppBar>
        <Container className="App">
        
          
          <Switch>
            <Route exact path='/'><Table dataTable={dataTable} setDataTable={setDataTable}/></Route>
            <Route path='/slider'><Slider dataTable={dataTable} setDataTable={setDataTable}/></Route>
          </Switch>
        </Container>
      </Router>
      </Context.Provider>
     
    </>
  );
}

export default App;
