import { Card, CardHeader, InputAdornment, TextField } from '@material-ui/core';
import {useContext, useEffect} from 'react';
import {Context} from './Context';
import {createStyles, makeStyles} from '@material-ui/core/styles'


const useStyles = makeStyles(theme => createStyles({
    cardHeader : {
        padding : theme.spacing(0)
    },
    root : {
        padding : theme.spacing(1),
        position: 'absolute',
        transition: 'all 0.3s ease',
        maxWidth: 150,
    }    
}))

export default function SliderForm(props){

    const {position , index , number } = props;
    const [dataTable, setDataTable] = useContext(Context);
    const classes = useStyles(); 


    let className = `${position[index]}`;

    const handleChangeValue = event => {
        dataTable[number].value = event.target.value;
        setDataTable({
            ...dataTable
        })
    }

    useEffect(()=>{
        let elem = document.querySelector('.center input');
        elem.focus()
    })

    return(
        <Card className={className + ' ' + classes.root} variant='outlined' >
                    <CardHeader 
                        className={classes.cardHeader}
                        title={`Параметр №${number}`}
                        titleTypographyProps={{ 
                            variant : 'body1',
                            align : 'right'
                            }}    
                    />
                        <TextField 
                            margin='normal'
                            variant='outlined'
                            InputProps={{
                               endAdornment: <InputAdornment position="start">мм</InputAdornment>,
                            }}
                            placeholder={`${dataTable[number] ? dataTable[number].value : position}`}
                            onChange={handleChangeValue}
                            onBlur={(event)=>{if (event.target.value)  handleChangeValue(event) }}
                            disabled = {position[index] !== 'center'}
                        />  
        </Card>
    )
}