import FormCell from './FormCell'
import {useState , useContext} from 'react';
import {Context} from './Context';
import { TableCell } from '@material-ui/core';
import {makeStyles} from '@material-ui/core/styles'

const useStyle = makeStyles({
    tableCell : {
        maxWidth: '80px',
    }
})

export default function MyTableCell(props){
    const classes = useStyle();
    const [dataTable] = useContext(Context);
    const [isForm, setIsForm] = useState(false);
    const parametr = dataTable[props.parametr.number];   

    return (
        <TableCell className={classes.tableCell} variant='body' onDoubleClick={()=> setIsForm(!isForm)}  >
          {isForm
            ? <FormCell 
            numberParametr={parametr.number} 
            setIsForm={setIsForm} /> 
            : parametr.value}
        </TableCell>
    )
};