import './Slider.css';
import {useState , useContext, useEffect} from 'react';
import SliderForm from './SliderForm';
import {Context} from './Context';
import {Link, BrowserRouter as Router} from 'react-router-dom';
import { Button, ButtonBase, Container, Grid, IconButton } from '@material-ui/core';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import { createStyles, makeStyles } from '@material-ui/core/styles'

const useStyles = makeStyles((theme) => createStyles({
    slider : {
        position: 'relative',
        height: 300,
        overflow: 'hidden',
    },
    link: {
        textDecoration: 'none',
        color : 'inherit',
    }
}))


export default function Slider(){
    const [dataTable] = useContext(Context);
    const data = Object.keys(dataTable);
    const [count, setCount] = useState(0);
    const position = ['leftmos', 'left' , 'center', 'right', 'rightmos'];
    const classes = useStyles();


    
    const [index3, setIndex3] = useState(2);
    const [index2, setIndex2] = useState(1);
    const [index1, setIndex1] = useState(0);
    const [index4, setIndex4] = useState(3);
    const [index5, setIndex5] = useState(4);
    


    const indexs = [
        {index : index2 , set: setIndex2 } ,
        {index : index5 , set: setIndex5 } ,
        {index : index1 , set: setIndex1 } ,
        {index : index3 , set: setIndex3 } ,
        {index : index4 , set: setIndex4 } ,
      ];
    const handleLeft = event => {
        if (count === 0) return null;
        setCount( c => c = count === 0 ? data.length-1 : count -1 );
        indexs.forEach( obj => {
            obj.set( i => i = obj.index === 0 ? position.length - 1 : obj.index -1 )
        })
    }

    const handleRight = event => {
        if (count === data.length-1) return null;
        setCount( c => c = count === data.length-1 ? 0 : count + 1); 
        indexs.forEach( obj => {
            obj.set( i => i = obj.index === position.length-1 ? 0 : obj.index+1 )
        })
    }

    return (
        <Container maxWidth='md'>
            <Grid container direction='column' alignItems='center' justify='center'>
                <Grid item container alignItems='center' justify='center' className={classes.slider}>
                    {indexs.map((obj , i) => ( data[count-obj.index+Math.floor(position.length/2)] ? <SliderForm 
                        index={obj.index} 
                        position={position}
                        number={ data[count-obj.index+Math.floor(position.length/2)]}
                        key = {i}
                        /> : null)
                    )}
                </Grid>
                <Grid item className='slider_btns_container'>
                    <IconButton onClick={handleLeft} aria-label='btn_back'>
                        <ArrowBackIosIcon />
                    </IconButton>
                    <Button variant='contained' color="primary"><Link to='/' className={classes.link}>Готово</Link></Button>
                    <IconButton onClick={handleRight} aria-label='btn_fwd'>
                        <ArrowForwardIosIcon />
                    </IconButton>
                </Grid>
            </Grid>
        </Container>
        
    )
}


