import {Table , TableHead ,TableRow , TableCell, TableContainer, Container, TableBody} from '@material-ui/core';
import {useContext} from 'react';
import MyTableCell from './TableCell';
import {Context} from './Context'
import {makeStyles} from '@material-ui/core/styles';

const parametrsLevel = ( data , level ) => {
    return (Object.keys(data)
        .map( numberParametr => data[numberParametr].level === level 
            ? data[numberParametr] 
            : '')
            .filter( p => p) 
    );
}

const useStyles = makeStyles({})

export default function MyTable(props){

    const classes = useStyles();
    const [dataTable, setDataTable] = useContext(Context);

    return(
        <div>
        <Container maxWidth='md'>
        <TableContainer>
            <Table className={classes.table}>
                <caption>Параметры отклонений РМ</caption>
                <TableHead className={classes.tableHead}>
                    <TableRow hover={true}>
                        <TableCell variant='head'>Шток</TableCell>
                        <TableCell variant='head'>191-194</TableCell>
                        <TableCell variant='head'>241-244</TableCell>
                        <TableCell variant='head'>321-324</TableCell>
                        <TableCell variant='head'>401-404</TableCell>
                        <TableCell variant='head'>411-414</TableCell>
                        <TableCell>471-474</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    <TableRow>
                        <TableCell>3P</TableCell>
                        {parametrsLevel(dataTable , 1)
                        .filter( param => param.gate === 3)
                        .sort( (a ,b) => a.number - b.number )
                        .map(par => <MyTableCell key={par.number} parametr={par} setDataTable={setDataTable} dataTable={dataTable}/>)}                  
                    </TableRow>  
                    <TableRow>
                        <TableCell>1P</TableCell>
                        {parametrsLevel(dataTable , 1)
                        .filter( param => param.gate === 1)
                        .sort( (a ,b) => a.number - b.number )
                        .map(par => <MyTableCell key={par.number} parametr={par} setDataTable={setDataTable} dataTable={dataTable}/>)}                  
                    </TableRow>
                    <TableRow>
                        <TableCell>4P</TableCell>
                        {parametrsLevel(dataTable , 1)
                        .filter( param => param.gate === 4)
                        .sort( (a ,b) => a.number - b.number )
                        .map(par => <MyTableCell key={par.number} parametr={par} setDataTable={setDataTable} dataTable={dataTable}/>)}                  
                    </TableRow>
                    <TableRow>
                        <TableCell>2P</TableCell>
                        {parametrsLevel(dataTable , 1)
                        .filter( param => param.gate === 2)
                        .sort( (a ,b) => a.number - b.number )
                        .map(par => <MyTableCell key={par.number} parametr={par} setDataTable={setDataTable} dataTable={dataTable}/>)}                  
                    </TableRow>  
                </TableBody>
            </Table>
        </TableContainer>
        </Container>
            
        </div>
    )
} 