import { Box, IconButton, InputAdornment, TextField } from '@material-ui/core';
import {useState, useContext} from 'react';
import {Context} from './Context';
import DoneIcon from '@material-ui/icons/Done';
import CloseIcon from '@material-ui/icons/Close';
import {makeStyles} from '@material-ui/core/styles';

const useStyle = makeStyles({
    formCell: {
        display: 'flex'
    },
    textField: {
        flexGrow : 1,
    }
})

export default function FormCell(props){

    const classes = useStyle();
    const [dataTable , setDataTable] = useContext(Context);
    const {numberParametr , setIsForm} = props;
    const [input, setInput] = useState(''); 

    const handleOk = event => {
        input && (dataTable[numberParametr].value = input);
        setIsForm(false);
        setDataTable({
            ...dataTable
        });
    };

    return (
        <Box className={classes.formCell} 
            onBlur={(event) => {
                if (!event.currentTarget.contains(event.relatedTarget)){
                setIsForm(false);
                }
            }}>
            <TextField
                className={classes.textField}
                InputProps={{
                    startAdornment: <InputAdornment position="start">мм</InputAdornment>,
                }} 
                onChange={ ({target}) => {setInput(target.value)}} 
                autoFocus/>
            
                <IconButton 
                    edge='end' 
                    size='small' 
                    onClick={handleOk} 
                    aria-label="agree" >
                        <DoneIcon />
                </IconButton>
                <IconButton 
                    edge='end' 
                    size='small'
                    onClick={()=> {setIsForm(false)}} 
                    aria-label="cancel">
                    <CloseIcon />
                </IconButton>
            
        </Box>
    )
}